.header {
  width: 100%;
  height: 90px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #f1f7ff;
}
.unit {
  width: 30%;
  display: flex;
  justify-content: space-around;
}
.unit a,
p {
  font-family: "TTT";
  color: rgba(30, 30, 30, 1);
  font-size: 14px;
}
.headLine {
  width: 100%;
  height: 1px;
  background-color: black;
}
.logIn {
  display: flex;
  width: 10%;
  justify-content: space-between;
}
.logIn p {
  color: rgba(30, 30, 30, 1);
  font-family: "TTT";
  font-size: 14px;
}
.logIn button {
  font-size: 14px;
  font-family: "TTT";
  border: 0px;
  background-color: #f1f7ff;
  cursor: pointer;
}
.link {
  position: relative;
}
.link::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 1px;
  background: #000;
}
.link::after {
  width: 0;
  transition: 0.3s;
}
.link:hover:after {
  width: 100%;
}
.link::after {
  left: 50%;
  transform: translateX(-50%);
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #ffffff;
  min-width: 160px;
  width: 270px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-content a {
  font-size: 14px;
  font-family: "TTT";
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  border-bottom: 2px solid rgba(33, 37, 40, 0.05);
}
.logo img {
  width: 200px;
}

.dropdown-content a:hover {
  background-color: #ddd;
}
.dropdown:hover .dropdown-content {
  display: block;
}
